<template>
  <div class="classify-item-card" @click="ClassifyDetails">
    <div class="img">
      <img :src="classItem.bgPicture" alt />
    </div>
    <div class="cover">
      <span>#{{classItem.name}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "classifyItemCard",
  props: ["classItem"],
  methods: {
    ClassifyDetails() {
      window.console.log(this.classItem.id);
      this.$router.push({
        path: "/classifydetails",
        query: {
          id: this.classItem.id
        }
      });
      this.$store.commit("setShowBottomBar", false);
      this.$store.commit("setClassifyDetailId", this.classItem.id);
    }
  }
};
</script>

<style lang="less" scoped>
.classify-item-card {
  position: relative;
  box-sizing: border-box;
  &:active{
    border: 2px solid red;
  }
  .img {
    img {
      display: block;
      width: 100%;
    }
  }
  .cover {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
    span {
      width: 50px;
      height: 50px;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      line-height: 50px;
      text-align: center;
    }
  }
}
</style>